.start{
  width: 10%;
}
.end{
  width: 10%;
}
.board{
  width: 80%;
  
}

.cell {
  background: #fff;
  border: .1px solid #414c69;
  float: left;
  color: #414c69;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  font-weight: bold;
}
/*border: .1px solid #414c69; height: 3vh; width: 3%;*/
.cell:focus {
  outline: olive;
}

.hidden {
  background: #7e7b92;
}

.is-flag,
.is-mine {
  color: #fc543c;
}

.screen-button{
  display: inline-block;
  text-decoration: none;
  background: #E59F2D;
  color: rgba(255, 255, 255, 0.47);
  font-weight: bold;
  font-size: 20pt;
  width: 120px;
  height: 120px;
  line-height: 120px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  overflow: hidden;
  box-shadow: 0px 0px 0px 5px #F4CD65;
  border: solid 2px rgba(255, 255, 255, 0.47);
  transition: .4s;
  cursor: pointer;
}

.screen-button:hover{
  border-style: dashed;
}
